import React from "react";
import { createUseStyles } from "react-jss";
import { Link } from "react-router-dom";
import { ThemeType } from "../../styles/theme";
import SearchIcon from "../../assets/search@2x.png";
import { SubTitle } from "../";
import { PRepListItem } from "../../types";

const usePRepItemStyles = createUseStyles((theme: ThemeType) => ({
  pRep: {
    width: "100%",
    display: "flex",
    justifyContent: "space-between",
    padding: `10px ${theme.gap}px`,
    color: theme.secondary1,
    borderRadius: 10,
    "&:hover": {
      background: theme.primary1,
      color: theme.mono1,
    },
  },
  "right-item": {
    display: "flex",
    fontWeight: theme.bold,
    "& p": {
      width: 10,
      textAlign: "right",
      "&:first-of-type": {
        marginRight: 114,
        [theme["breakpoint-xs"]]: {
          marginRight: 50,
        },
      },
    },
  },
}));

interface PRepItemProps {
  name: string;
  openCnt: number;
  closeCnt: number;
  link: string;
}

const PRepItem: React.SFC<PRepItemProps> = (props) => {
  const classes = usePRepItemStyles(props);
  const { link, openCnt, closeCnt, name } = props;
  return (
    <Link to={link}>
      <div className={classes.pRep}>
        <p>{name}</p>
        <div className={classes["right-item"]}>
          <p>{openCnt}</p>
          <p>{closeCnt}</p>
        </div>
      </div>
    </Link>
  );
};

interface PRepListProps {
  data: {
    [key: string]: PRepListItem[];
  };
}

const usePRepListStyles = createUseStyles((theme: ThemeType) => ({
  wrap: {
    marginTop: 35,
    marginBottom: 60,
  },
  searchBar: {
    width: "100%",
    backgroundColor: theme.secondary3,
    background: `url(${SearchIcon}) ${theme.gap}px center no-repeat`,
    backgroundSize: "18px 18px",
    fontFamily: theme.font,
    fontWeight: theme.bold,
    padding: `10px ${theme.gap}px`,
    borderRadius: 20,
    marginBottom: 40,
    "& input": {
      width: "98%",
      marginLeft: theme.gap,
      color: theme.secondary2,
    },
    "& input::placeholder": {
      color: theme.secondary2,
    },
  },
  letterSection: {
    width: "100%",
    position: "relative",
  },
  cntLabel: {
    position: "absolute",
    fontSize: "2.4rem",
    fontWeight: theme.bold,
    color: theme.secondary1,
    marginBottom: 30,
    display: "flex",
    right: 0,
    background: theme.mono1,
    "& p": {
      width: 124,
      textAlign: "right",
      [theme["breakpoint-xs"]]: {
        width: 70,
        fontSize: "1.8rem",
      },
    },
  },
}));

const PRepList: React.SFC<PRepListProps> = (props) => {
  let _i = 0;
  const classes = usePRepListStyles(props);
  const { data } = props;
  return (
    <div className={classes.wrap}>
      <div className={classes.searchBar}>
        <input type="text" placeholder="Search P-Reps" />
      </div>
      {Object.entries(data).map(([key, arr]) => {
        _i++;
        return (
          <div key={key} className={classes.letterSection}>
            {_i === 1 && (
              <div className={classes.cntLabel}>
                <p>Open</p>
                <p>Close</p>
              </div>
            )}
            <SubTitle text={key} />
            {arr.map((item) => (
              <PRepItem
                key={item.pRepName}
                name={item.pRepName}
                openCnt={0}
                closeCnt={0}
                link={`/proposals/${item.username}`}
              />
            ))}
          </div>
        );
      })}
    </div>
  );
};

export default PRepList;

