import React, { useCallback } from "react";
import { createUseStyles } from "react-jss";
import { ThemeType } from "../styles/theme";
import {
  Title,
  PRepList,
  Button,
  ButtonType,
  ProposalCardList,
} from "../components";
import { useHistory } from "react-router-dom";
import Mock from "../constants/mock";
import { PRepListContainer, ProposalListContainer } from "../containers";

const useStyles = createUseStyles((theme: ThemeType) => ({
  main: {
    marginTop: 18,
  },
  main__title: {
    display: "flex",
    alignItems: "baseline",
    marginBottom: 42,
  },
}));

const MainPage: React.SFC = () => {
  const history = useHistory();
  const classes = useStyles();

  const handleClick = useCallback(() => {
    history.push("/latest-proposals");
  }, [history]);
  return (
    <div className="container">
      <div className={classes.main}>
        <div className={classes.main__title}>
          <Title text="Latest proposals" />
          <Button
            onClick={handleClick}
            text="View more"
            buttonType={ButtonType.FLAT}
          />
        </div>
        <div>
          <ProposalListContainer hasFilter={false} />
        </div>
        <div>
          <Title text="P-Reps with proposals" />
          <PRepListContainer />
        </div>
      </div>
    </div>
  );
};

export default MainPage;
