import React, { useCallback } from "react";
import { createUseStyles } from "react-jss";
import { Link, useHistory } from "react-router-dom";
import { ThemeType } from "../../styles/theme";
import VotedIcon from "../../assets/voted-icon@2x.png";
import { ProposalModel, VoteModel } from "../../types";
import { ProposalOption, ProposalMetaData } from "..";

function isVoteModel(obj: VoteModel | ProposalModel): obj is VoteModel {
  return (obj as VoteModel).selectedOption !== undefined;
}

interface CardProps {
  item: ProposalModel | VoteModel;
}

const useCardStyles = createUseStyles((theme: ThemeType) => ({
  wrap: {
    position: "relative",
    width: 320,
    marginRight: "calc((100% - 960px) / 2)",
    marginBottom: 48,
    padding: theme.gap,
    boxShadow: `0px 8px 20px -2px rgba(0,107,131,0.15)`,
    border: `1px solid ${theme.secondary3}`,
    borderRadius: 10,
    height: "100%",
    cursor: "pointer",

    "&:nth-child(3n)": {
      marginRight: 0,
    },

    "&::after": {
      boxShadow: `0px 13px 20px -2px rgba(0,184,204,0.25)`,
      opacity: 0,
      transition: "opacity 300ms",
      content: "''",
      position: "absolute",
      top: 0,
      left: 0,
      bottom: 0,
      right: 0,
      width: "100%",
      height: "100%",
      zIndex: -1,
    },
    "&:hover::after": {
      opacity: 1,
    },
  },
  [theme["breakpoint-sm"]]: {
    wrap: {
      width: "100%",
    },
  },
  pRepName: {
    fontSize: "1.6rem",
    color: theme.secondary1,
    marginBottom: 8,
  },
  title: {
    fontSize: "1.8rem",
    fontWeight: theme.bold,
    color: theme.secondary1,
    marginBottom: 18,
    lineHeight: "2.5rem",
  },
  options: {
    width: "100%",
    height: 160,
  },
  selectedOption: {
    width: "100%",
    background: `url(${VotedIcon}) left center no-repeat`,
    backgroundSize: "23.27px 25px",
    paddingLeft: 35,
    fontWeight: theme.semiBold,
    color: theme.secondary1,
    marginBottom: 18,
    "& + p": {
      color: theme.secondary1,
      fontWeight: theme.semiBold,
      fontSize: "1.4rem",
    },
  },
}));

const Card: React.SFC<CardProps> = (props) => {
  const history = useHistory();
  const classes = useCardStyles(props);

  const { item } = props;

  // TODO: implement endsAt -> closed, voted
  const {
    pRepName,
    pRepLink,
    title,
    options,
    proposalType,
    endsAt,
    proposalLink,
    status,
  } = item;

  const isVoteItem = useCallback(() => {
    return isVoteModel(item);
  }, [item]);

  const onClick = useCallback(() => {
    history.push(proposalLink);
  }, [history, proposalLink]);

  return (
    <div onClick={onClick} className={classes.wrap}>
      <p className={classes.pRepName}>
        <Link to={pRepLink}>{pRepName}</Link>
      </p>
      <p className={classes.title}>{title}</p>
      {isVoteItem() ? (
        <>
          <div className={classes.selectedOption}>
            {options[(item as VoteModel).selectedOption].name}
          </div>
          <p>{proposalType}</p>
        </>
      ) : (
        <>
          <div className={classes.options}>
            {options.map((option, i) => (
              <ProposalOption key={option.name} option={option} />
            ))}
          </div>
          <ProposalMetaData
            status={status}
            proposalType={proposalType}
            endsAt={endsAt}
          />
        </>
      )}
    </div>
  );
};

export default Card;
