import React, { useState } from "react";
import { createUseStyles } from "react-jss";
import { ThemeType } from "../../styles/theme";
import { Input, ErrorMessage } from "../";
import EyeIcon from "../../assets/eye@2x.png";
import Fade from "./Fade";

interface Props {}

const useStyles = createUseStyles((theme: ThemeType) => ({
  toggle: {
    position: "absolute",
    right: 0,
    paddingRight: 32,
    color: theme.primary1,
    marginTop: -2,
    fontWeight: theme.semiBold,
    background: `url(${EyeIcon}) center right no-repeat`,
    backgroundSize: "24px 18px",
    cursor: "pointer",
  },
}));

const PasswordInput: React.SFC<Props> = (props) => {
  const classes = useStyles(props);
  const [passwordShown, setPasswordShown] = useState<boolean>(false);
  const [password, setPassword] = useState<string>("");
  const [error, setError] = useState<string>("");

  React.useEffect(() => {
    const id = setTimeout(() => {
      setError("1234");
      clearTimeout(id);
    }, 1500);
    const id2 = setTimeout(() => {
      setError("");
      clearTimeout(id2);
    }, 3000);
  }, []);

  return (
    <>
      <div
        onClick={() => setPasswordShown(!passwordShown)}
        className={classes.toggle}
      >
        {passwordShown ? "Hide" : "Show"}
      </div>
      <Input
        label="Password"
        value={password}
        onChange={(e) => setPassword((e.target as HTMLInputElement).value)}
        type={passwordShown ? "text" : "password"}
      />
      <Fade isShown={!!error}>
        <ErrorMessage text="Error Message Show/Hide Animation Sample." />
      </Fade>
    </>
  );
};

export default PasswordInput;
