export enum ProposalType {
  FANS = "Fans only",
  COMMUNITY = "Community",
}

export enum ProposalStatusType {
  VOTING = "VOTING",
  APPROVED = "APPROVED",
  REJECTED = "REJECTED",
  CANCELLED = "CANCELLED",
}

/* View Model */

export interface ProposalOptionModel {
  name: string;
  percent: number;
}

export interface ProposalModel {
  pRepName: string;
  title: string;
  options: ProposalOptionModel[];
  proposalType: ProposalType;
  endsAt: number;
  pRepLink: string;
  proposalLink: string;
  status: ProposalStatusType;
}

export interface VoteModel extends ProposalModel {
  selectedOption: number;
}

/* Data Model */

export interface Proposal {
  ID: string;
  address: string;
  contents: string;
  electoral_threshold: string;
  expire_timestamp: number;
  final: string;
  subject: string;
  status: ProposalStatusType;
  expire_date: string;
  select_item: string[];
  winning_threshold: string;
  transaction: string;
  winner: number;
}

export interface PRep {
  address: String;
  status: String;
  penalty: String;
  grade: String;
  name: String;
  country: String;
  city: String;
  stake: String;
  delegated: String;
  totalBlocks: String;
  validatedBlocks: String;
  unvalidatedSequenceBlocks: String;
  irep: String;
  irepUpdateBlockHeight: String;
  lastGenerateBlockHeight: String;
  blockHeight: String;
  txIndex: String;
  email: String;
  website: String;
  details: String;
  p2pEndpoint: String;
}

export interface PRepListItem {
  username: string;
  address: string;
  pRepName: string;
}
