import React, { useEffect } from "react";
import { createUseStyles } from "react-jss";
import { useLocation } from "react-router-dom";
import useSWR from "swr";
import { ThemeType } from "../styles/theme";
import { ProposalCardList, Loader } from "../components";
import Query from "../apis/query";
import { ProposalType, ProposalModel, Proposal } from "../types";

interface Props {
  hasFilter: boolean;
}

const ProposalListContainer: React.SFC<Props> = (props) => {
  const { pathname } = useLocation();
  let fetchKey: string = "";
  let fetcher: () => Promise<Proposal[]>;

  if (pathname.includes("/proposals/")) {
    const username = pathname.split("/")[2];
    fetchKey = `get_proposals_${username}`;
    fetcher = () =>
      Query.getProposals({
        _proposer: username,
        _start_proposal_id: 0,
        _end_proposal_id: Number.MAX_SAFE_INTEGER,
      });
  } else if (pathname === "/latest-proposals") {
    fetchKey = "get_recent_proposals";
    fetcher = () =>
      Query.getProposals({
        _proposer: "ICXstation",
        _start_proposal_id: 0,
        _end_proposal_id: Number.MAX_SAFE_INTEGER,
      });
  } else {
    fetchKey = "get_recent_proposals";
    fetcher = () =>
      Query.getProposals({
        _proposer: "ICXstation",
        _start_proposal_id: 0,
        _end_proposal_id: Number.MAX_SAFE_INTEGER,
      });
  }

  const { data = [], isValidating } = useSWR(fetchKey, fetcher);

  if (data.length === 0 && isValidating) {
    return <Loader height={438} />;
  }

  const proposals: ProposalModel[] = data!.map((item) => {
    return {
      pRepName: "ICXstation",
      title: item.subject,
      options: item.select_item.map((name: string) => ({
        name,
        percent: 20,
      })),
      proposalType: ProposalType.FANS,
      endsAt: item.expire_timestamp,
      pRepLink: "",
      proposalLink: "/proposal/icx-station/1",
      status: item.status,
    };
  });

  return <ProposalCardList proposals={proposals} hasFilter={props.hasFilter} />;
};

export default ProposalListContainer;
