import React, { useEffect } from "react";
import {
  Route,
  Switch,
  Redirect,
  RouteProps,
  useLocation,
} from "react-router-dom";
import { Header, Footer } from "./components";
import {
  MainPage,
  AboutPage,
  PRepDetailPage,
  SignInPage,
  SignUpPage,
  VerifyAddressPage,
  EditProposalPage,
  ProposalDetailPage,
  LatestProposalPage,
  ProfilePage,
} from "./pages";

function Routes() {
  const location = useLocation();
  useEffect(() => {
    window.scrollTo(0, 0);
  }, [location]);

  return (
    <>
      <Header isLoggedIn={false} />
      <div>
        <Switch>
          <Route exact path="/" component={MainPage} />
          <Route exact path="/about" component={AboutPage} />
          <Route path="/latest-proposals" component={LatestProposalPage} />
          <Route exact path="/proposals/:username" component={PRepDetailPage} />
          <PrivateRoute exact path="/proposal/:username/edit">
            <EditProposalPage />
          </PrivateRoute>
          <Route
            exact
            path="/proposal/:username/:proposalId"
            component={ProposalDetailPage}
          />
          <Route exact path="/signin" component={SignInPage} />
          <Route exact path="/signup" component={SignUpPage} />
          <PrivateRoute exact path="/verify">
            <VerifyAddressPage />
          </PrivateRoute>
          <PrivateRoute exact path="/profile">
            <ProfilePage />
          </PrivateRoute>
        </Switch>
      </div>
      <Footer />
    </>
  );
}

function PrivateRoute({
  children,
  ...rest
}: RouteProps & { children: React.ReactNode }) {
  return (
    <Route
      {...rest}
      render={({ location }) =>
        true ? (
          children
        ) : (
          <Redirect
            to={{
              pathname: "/login",
              state: { from: location },
            }}
          />
        )
      }
    />
  );
}

export default Routes;
