import React from "react";
import clsx from "clsx";
import { createUseStyles, useTheme } from "react-jss";
import { Link, useHistory } from "react-router-dom";
import Logo from "../assets/logo-all@2x.png";
import { ThemeType } from "../styles/theme";
import { Button, ButtonType, MobileMenu } from "./";

interface Props {
  isLoggedIn: boolean;
}

const useStyles = createUseStyles((theme: ThemeType) => ({
  wrap: {
    display: "flex",
    justifyContent: "space-between",
    alignItems: "center",
    paddingTop: 56,
    marginBottom: 54,
  },
  "right-items": {
    display: "flex",
    "& > div": {
      marginRight: 26,
      "&:last-of-type": {
        marginRight: 0,
      },
    },
    [theme["breakpoint-sm"]]: {
      display: "none",
    },
  },
  logo: {
    width: 230,
    cursor: "pointer",
    [theme["breakpoint-xs"]]: {
      width: 150,
    },
  },
}));

const Header: React.SFC<Props> = (props) => {
  const theme = useTheme();
  const classes = useStyles({ ...props, theme });
  const { isLoggedIn } = props;
  const history = useHistory();

  const onClick = (link: string) => {
    history.push(link);
  };

  return (
    <header className={clsx(classes.wrap, "container")}>
      <Link to="/">
        <img className={classes.logo} src={Logo} alt="ICON VOTE" />
      </Link>
      <div className={classes["right-items"]}>
        <Button
          onClick={() => onClick("/about")}
          text="About"
          buttonType={ButtonType.FLAT}
        />
        <Button
          onClick={() => onClick("/proposal/icx-station/edit")}
          text="New Proposal"
          buttonType={ButtonType.FLAT}
        />
        {isLoggedIn ? (
          <div></div>
        ) : (
          <Button text="Sign in" onClick={() => onClick("/signin")} />
        )}
      </div>
      <MobileMenu isLoggedIn={false} />
    </header>
  );
};

export default Header;
