import React from "react";
import { createUseStyles } from "react-jss";
import { ThemeType } from "../../styles/theme";
import { Card, SubTitle, Dropdown, DropdownType } from "..";
import { ProposalModel } from "../../types";

import AllIcon from "../../assets/all-icon@2x.png";
import CommunityIcon from "../../assets/community-icon@2x.png";
import FansIcon from "../../assets/fan-icon@2x.png";
import AllIconHover from "../../assets/all-icon--hover@2x.png";
import CommunityIconHover from "../../assets/community-icon--hover@2x.png";
import FansIconHover from "../../assets/fan-icon--hover@2x.png";

interface Props {
  proposals: ProposalModel[];
  hasFilter: boolean;
}

const useStyles = createUseStyles((theme: ThemeType) => ({
  header: {},
  wrap: {
    display: "flex",
    flexWrap: "wrap",
  },
}));

const ProposalCardList: React.SFC<Props> = (props) => {
  const classes = useStyles(props);
  const { proposals, hasFilter } = props;

  return (
    <>
      {hasFilter && (
        <div className={classes.header}>
          <SubTitle
            style={{ marginBottom: 54 }}
            text="Proposals"
            dropdown={
              <Dropdown
                menuList={[
                  {
                    icon: AllIcon,
                    iconHover: AllIconHover,
                    iconSize: "34px 20px",
                    text: "All",
                    onClick: () => {},
                  },
                  {
                    icon: CommunityIcon,
                    iconHover: CommunityIconHover,
                    iconSize: "24px 20px",
                    text: "Community",
                    onClick: () => {},
                  },
                  {
                    icon: FansIcon,
                    iconHover: FansIconHover,
                    iconSize: "24px 20px",
                    text: "Fans only",
                    onClick: () => {},
                  },
                ]}
                dropdownType={DropdownType.FILTER}
              />
            }
          />
        </div>
      )}
      <div className={classes.wrap}>
        {proposals.map((proposal, i) => {
          return <Card key={i} item={proposal} />;
        })}
      </div>
    </>
  );
};

export default ProposalCardList;
