const _REQ_EVENT = "ICONEX_RELAY_REQUEST";
const _RES_EVENT = "ICONEX_RELAY_RESPONSE";

const requestAddress = async (address: string) => {
  return await new Promise((resolve) => {
    const customEvent = new CustomEvent(_REQ_EVENT, {
      detail: {
        type: "REQUEST_ADDRESS",
      },
    });
    window.addEventListener(_RES_EVENT, (res: any) => {
      const { type, payload } = res.detail;
      if (type === "RESPONSE_ADDRESS") {
        resolve(payload);
      }
    });
    window.dispatchEvent(customEvent);
  });
};

const requestTx = async (rawTx: any) => {
  return await new Promise((resolve) => {
    const customEvent = new CustomEvent(_REQ_EVENT, {
      detail: {
        type: "REQUEST_JSON-RPC",
        payload: JSON.parse(rawTx),
      },
    });
    window.addEventListener(_RES_EVENT, (res: any) => {
      const { type, payload } = res.detail;
      if (type === "RESPONSE_JSON-RPC") {
        resolve(payload);
      }
    });
    window.dispatchEvent(customEvent);
  });
};

export default {
  requestAddress,
  requestTx,
};
